* {
    padding: 0;
    margin: 0;
}

.navigation-container {
    width: 100%;
    background-color: rgb(60, 113, 219);
}

.navigation-items {
    display: flex;
}

.navigation-item {
    padding: 20px 40px;
    color: black;
    text-decoration: none;
    transition: background-color 0.3s ease-in-out;
}

.navigation-item:hover {
    background-color: rgba(75, 175, 198, 0.585);
}

.body-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.carousel-container {
    position: relative;
    border: 2px solid rgb(146, 176, 237);
    width: 1000px;
    height: 500px;
}

.carousel-container > button {
    position: absolute;
    bottom: 0;
    width: 100px;
    height: 50px;}

.carousel-container > button:nth-child(1) {
    left: 0;
}

.carousel-container > button:nth-child(2) {
    right: 0;
}

.carousel-img {
    width: 500px;
}

.navigation-about-us{
    color:Tomato; 
}

